					isDu: '是',
					Suitableproject: '上衣，非纸质围涎，游泳衣，雨衣，舞衣，鞋，帽，袜，手套（服装），围巾',
					CombinationType: '中文+拼音',
					time: '2030-08-27'
				},
				DetailedD: {
					shopDetail: '25类-服装鞋帽',
					add: '全国',
				},
				imgs: [{
						img: '/static/images/lzc/deatil/tu1.png'
					},
					{
						img: '/static/images/lzc/deatil/tu1.png'
					},
					{
						img: '/static/images/lzc/deatil/tu2.png'
