					hpic: '/static/images/logo.png',
					tel: '15135725603',
					time: '刚刚',
					con: '沟通很到位，确定完信息就可以开始办理了'
				}],
				info: {
					hpic: '/static/images/logo.png',
					tel: '15135725603',
					time: '刚刚'
				}
			}
		},
		onHide() {

		},
		onLoad() {
			that = this
		},
		onShow() {

		},

		computed: {
			...mapState(['hasLogin', 'forcedLogin', 'userName', 'userinfo'])
		},
		methods: {
			...mapMutations(['logout', 'login']),
			getimg(img) {
				return api.getimg(img)
			},
			jump(e) {
				return api.jump(e)
			},
			back() {
				uni.navigateBack({
					delta: 1
				})
			},
			liu() {
				this.isLiu = true;
			},
			delLiu() {
				this.isLiu = false;
			},
			liuBtn() {
				uni.showToast({
					title: '留言成功',
					duration: 1000,
					icon: 'none',
					success: () => {
						this.liuText = '';
					}
				})
			},
			ShouClick() {
				this.isShou = !this.isShou;
				if (this.isShou == true) {
					uni.showToast({
						title: '取消收藏',
						duration: 1000,
						icon: 'none'
					})
				} else {
					uni.showToast({
						title: '收藏成功',
						duration: 1000,
						icon: 'none'
					})
				}
			}
		}
	}
</script>

